body {
  width: 100%;
  max-width: 1440px;
  background: radial-gradient(circle, #eeaeca 0%, #94bbe9 100%);
  margin: 20px auto;
}

.container {
  margin-left: auto;
  margin-right: auto;
}

.cat-info {
  gap: 40px;
  margin: 40px 20px 0;
  display: flex;
}

img {
  box-shadow: 5px 5px 5px 1px rgba(51, 51, 51, .2);
}

.box-dascr {
  width: 100%;
}

.loader, .error {
  display: none;
}

.loader-spin {
  width: 20px;
  height: 20px;
  box-sizing: border-box;
  border: 2px solid #fff;
  border-bottom-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.notiflix-notify {
  position: absolute;
  top: 328%;
  left: -100%;
  transform: translate(-328%, 100%);
}

